<template>
  <div style>
    <!-- <mu-appbar style="width: 100%;" class="header backgroundNaviBar">
      <a @click="backTo()">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span style="color:white">忘记密码</span>
    </mu-appbar>-->
    <van-nav-bar
      title="忘记密码"
      @click-left="()=>{
          $router.go(-1)
         }"
      @click-right="backTo"
      left-arrow
      class="backgroundNaviBar"
      z-index="999"
      :border="false"
    >
      <!-- <van-icon name="filter-o" slot="right" size="20" /> -->
    </van-nav-bar>
    <div class="forgetContent">
      <div class="flexForget">
        <div class="imageForgetPassword">
          <img src="../../../static/img/user.png" alt srcset />
        </div>
        <div style="flex:1">
          <input class="inputNews" type="text" placeholder="请输入您的用户名" v-model="account" />
        </div>
      </div>
      <!-- <div class="flexForget">
        <div class="imageForgetPassword">
          <img src="../../../static/img/IDcard.png" alt srcset />
        </div>
        <div style="flex:1">
          <input class="inputNews" type="text" placeholder="请输入您的身份证" v-model="identity" />
        </div>
      </div>-->
      <div class="flexForget">
        <div class="imageForgetPassword">
          <img src="../../../static/img/iphone.png" alt srcset />
        </div>
        <div style="flex:1">
          <input class="inputNews" type="text" placeholder="请输入您的电话号码" v-model="phone" />
        </div>
      </div>
      <div class="flexForget">
        <div class="imageForgetPassword">
          <img src="../../../static/img/forgetPassword.png" alt srcset />
        </div>
        <div style="flex:1">
          <input class="inputNews" type="password" placeholder="请输入您的新密码" v-model="newPassword" />
        </div>
      </div>
      <div class="flexForget">
        <div class="imageForgetPassword">
          <img src="../../../static/img/forgetPassword.png" alt srcset />
        </div>
        <div style="flex:1">
          <input class="inputNews" type="password" placeholder="确认您的新密码" v-model="newPassword1" />
        </div>
      </div>
    </div>
    <div
      class="submit backgroundNaviBar"
      :style="'background:'+$store.state.color"
      @click="submitBefore()"
    >
      <span>提交</span>
    </div>
    <van-overlay :show="showover" @click="showover = false" class="overlay">
      <van-loading type="spinner" color="#1989fa" />
    </van-overlay>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax.js";
import Vue from "vue";
import { Toast } from "vant";
import { Dialog, Loading } from "vant";
Vue.use(Toast);
// import Vue from 'vue';
import { Overlay } from "vant";
Vue.use(Overlay).use(Loading);
export default {
  name: "forget",
  data() {
    return {
      account: "",
      identity: "",
      newPassword: "",
      phone: "",
      showover: false,
      newPassword1: ""
    };
  },
  methods: {
    submitBefore() {
      var _this = this;
      Dialog.confirm({
        title: "更新",
        message: "请确认信息是否完善？"
      })
        .then(() => {
          // on confirm
          _this.submit();
          _this.showover = true;
        })
        .catch(() => {
          // on cancel
        });
    },
    backTo() {
      this.$router.go(-1);
    },
    async submit() {
      if (this.account === "" || this.newPassword == "" || this.phone === "") {
        this.showover = false;
        Toast.fail("请完善信息！");
      } else {
        var url = "/system/user/forgetPassword";
        var account = this.account;
        var identity = this.identity;
        var newPassword = this.newPassword;
        var phone = this.phone;
        if (this.newPassword !== this.newPassword1) {
          Toast("更改的密码不一致，请确认！");
          this.showover = false;
        } else {
          var res = await AppBace.putdata(url, "PUT", {
            account,
            identity,
            newPassword,
            phone
          });
          console.log(res);
          if (res.data.message === "success") {
            Toast.success("修改完成！");
            this.$router.go(-1);
            this.showover = false;
          } else {
            Toast.fail("更新失败，请重试！");
            this.showover = false;
          }
        }
      }
    }
  },
  mounted() {}
};
</script>
<style scoped>
.flexForget {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 40px;
  align-items: center;
  border-bottom: 1px solid rgb(238, 238, 238);
  margin-bottom: 15px;
}
.forgetContent {
  margin: 10px;
  background: white;
  padding: 15px;
  border-radius: 5px;
}
.inputNews {
  /* border-radius: 5px;  */
  border: none;
  outline: none;
  width: 100%;
  padding: 0 10px;
}
.imageForgetPassword img {
  width: 24px;
  height: 24px;
}
.submit {
  margin: 10px;
  background: #4ed995;
  display: flex;
  justify-content: center;
  border-radius: 10px;
  height: 40px;
  align-items: center;
  font-weight: 600;
  color: white;
}
</style>


